import React, { useState } from "react";
import img1 from '../assets/1.jpg';
import img2 from '../assets/2.jpg';
import img3 from '../assets/3.jpg';
import img4 from '../assets/4.jpg';

function Testimonial(){
    let nums = [0,1,2,3,4];
    var [order, setOrder] = useState(nums);
    console.log(order);
    const testimonies = [
        {id:'1', name:'John Smith DC', image: img1, content:'“The Chiropractic Newsletter is well overdue to our industry. It provides a perfect (and low-cost) way of staying in front of our patients in the midst of schedules that get tighter each and every day.”'},
        {id:'2', name:'Helen Adams DC', image: img2, content:'“This a great product and will be of tremendous benefit to chiropractors trying to market their practices. It is an absolutely amazing marketing powerhouse giving the user far greater flexibility than anything I have seen on the market”'},
        {id:'3', name:'Scott Thomas DC', image: img3, content:'“The Chiropractic Newsletter system has allowed me to remind my patients about the benefits of chiropractic on a consistent basis It helps remind the community about the importance of chiropractic and its benefits.”'},
        {id:'4', name:'Elizabeth Thompson DC', image: img4, content:'“The Chiropractic Newsletter system has allowed me to remind my patients about the benefits of chiropractic on a consistent basis It helps remind the community about the importance of chiropractic and its benefits.”'},

    ];
    const testiset = testimonies.map((t)=>
        <div key={t.id} className={"col-md-3 text-center text-white order-"+order[t.id]}>
            <div className="bluebar">
                <div className="pb-3 display-6">
                    <i className="fa fa-commenting"/>
                </div>
                <img src={t.image} alt="testimonial image 1" className="round"/>
                <p className="py-4">{t.content}</p>
                <h5 className="text-end">- {t.name}</h5>
            </div>
        </div>
    );
    
    const previous = () => {
        order = order.map((n) => (n===1 ? n+3 : n-1));
        setOrder(order);
    }

    const next = () => {
        order = order.map((n) => (n===4 ? n-3 : n+1));
        setOrder(order);
    }
    return(
        
        <section className="bluebar py-5">
            <button className="btn btn-warning" onClick={previous}>Previous</button>
            <div className="container position-relative">
                <div className="row testimonial-group">
                    {testiset}
                </div>
            </div>
            <button className="btn btn-success" onClick={next}>Next</button>
        </section>
        
    );
}

export default Testimonial;