import React from "react";

function BlueTitleBar(props){
  const title = props.title;
  var color;
  (props.color) ? color = props.color : color = "bluebar";

  return(
    <section className={color + " py-4"}>
      <div className="container">
        <div className="row">
          <div className="col-lg-8 mx-auto">
            <h3 className="text-white text-bold text-center">{title}</h3>
          </div>
        </div>
      </div>
    </section>
  );
}

export default BlueTitleBar;