import React from 'react';
import Home_banner from '../components/home-banner';
import BlueTitleBar from '../components/BlueTitleBar';
import Sixlogos from '../components/sixlogos';
import ContactForm1 from '../components/ContactForm1';
import Testimonial from '../components/Testimonial';
import cngif from '../assets/cn-gif-1.gif';
import img1 from '../assets/chiropractic-newsletter-template-med.png';

function Home(){
  const title_string = "Find Out Why Thousands of Successful Chiropractors Use CHIROPRACTIC NewsletterTM to Market Their Clinics Every Month";
  const sixlogo1 = [
    {id:'1', icon:'fa-gift', text:'Attract New Patients'},
    {id:'2', icon:'fa-sitemap', text:'Stimulate Patient Referrals'},
    {id:'3', icon:'fa-cogs', text:'Distinguish Your Practice'},
    {id:'4', icon:'fa-users', text:'Market Your Practice'},
    {id:'5', icon:'fa-line-chart', text:'Build Strong Patient Relationships'},
    {id:'6', icon:'fa-plus-square', text:'Define Your Treatment Niche'},
  ];

  const sixlogo2 = [
    {id:'1', icon:'fa-file-text-o', text:'Personalize the appearance and feel of your clinic’s newsletter, utilizing custom headers, colors and footers'},
    {id:'2', icon:'fa-pencil', text:'Add content – Communicate in your own “voice” forging a relationship with your patients that sets you and your clinic apart from all other competitors'},
    {id:'3', icon:'fa-pencil-square', text:'Edit content – Remove any part of the newsletter to better reflect your specific treatment philosophy'},
    {id:'4', icon:'fa-picture-o', text:'Edit any picture or logo – There are 7 templates to choose from, as well as unlimited color selections'},
    {id:'5', icon:'fa-pencil-square-o', text:'Use Chiropractic Newsletter’s advanced editor to format'},
    {id:'6', icon:'fa-usd', text:'Save hundreds of dollars on copywriting fees charged by professional writers'},
  ];

  return(
    <div>
      <Home_banner />

      <div className='container py-5'>
        <div className='row'>
          <div className='col-md-6'>
            <img src={cngif} alt='cngif' className='img-fluid' />
          </div>
          <div className='col-md-6'>
          <img src={cngif} alt='cngif' className='img-fluid' />
          </div>
        </div>
      </div>

      <BlueTitleBar title={title_string}/>

      <section id="sixlogos1" className='py-5'>
        <div className='container'>
          <div className='row'>
            <div className='col-lg-10 mx-auto'>
              <Sixlogos content={sixlogo1}/>
            </div>
          </div>
        </div>
      </section>

      <BlueTitleBar title="NEWSLETTERS DELIVERED: 97,384,497"/>

      <section id="testimonial">
        <div className='container py-5'>
          <div className='row'>
            <div className='col-md-6'>
              <img src={img1} alt='img1' className='img-fluid' />
            </div>
            <div className='col-md-6'>
              <ContactForm1/>
            </div>
          </div>
        </div>
      </section>
      

      <Testimonial/>

      <BlueTitleBar color="darkbluebar" title="Who Wants To Deliver a Clear and Compelling Newsletter that Attracts New Patients and Influences Existing Patients to Refer Family and Friends to Your Clinic... Guaranteed?"/>

      <section id="sixlogos2" className='py-5'>
        <div className='container'>
          <div className='row'>
            <div className='col-lg-10 mx-auto'>
              <Sixlogos content={sixlogo2}/>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Home;