import React from "react";

function ContactForm1() {
    return (
        <form>
            <div className="mb-3">
                <label className="form-label">Email address</label>
                <input type="email" className="form-control" id="Email1" aria-describedby="emailHelp"></input>
            </div>
            <div className="mb-3">
                <label className="form-label">Password</label>
                <input type="password" className="form-control" id="Password1"></input>
            </div>
            <div className="mb-3">
                <label className="form-label">Phone</label>
                <input type="text" className="form-control" id="Phone1"></input>
            </div>
            <button type="submit" className="btn btn-primary">Submit</button>
        </form>
    );
}

export default ContactForm1;