import React from "react";

function Sixlogos(props){
  
  const elements = props.content.map((elem)=>
    <div className="col-md-4 text-center circle-icons my-4" key={elem.id}>
      <i className={"fa "+ elem.icon}></i>
      <h5 className="my-4 fw-normal">{elem.text}</h5>
    </div>
  );
  return(
      <div className="row">
        {elements}
      </div>
  );
}

export default Sixlogos;